* {
  box-sizing: border-box; 
}


body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
        Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif; /*각 운영체제 별로 기본 시스템 폰트를 로드하는 CSS 코드 */
    background-color: #eff3f7;
    height: 100%;
}

html, body, /*본문의 브라우저 높이를 100%로 만들 때, 본문 내용이 적을 시 밑에 백그라운드 컬러가 적용안된 흰색 바가 생김*/
#root, .container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center; /*속성은 플렉스 요소의 수평 방향 정렬 방식을 설정*/
    align-items: center; /*속성은 플렉스 요소의 수평 방향 정렬 방식을 설정*/
    font-weight: bolder;
    font-size: 100px;
    font-style: italic;
}

.movies {
    display: flex;
    justify-content: space-between; /*플렉스 요소는 요소들 사이에만 여유 공간을 두고 배치됩니다.*/
    align-items: flex-start;
    flex-wrap: wrap;   /* 속성은 플렉스 라인에 더 이상의 여유 공간이 없을 때, 플렉스 요소의 위치를 다음 줄로 넘길지를 설정*/
    padding: 50px;
    padding-top: 70px;
    width: 80%;
}

.movies .movie {
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px; /*테두리를 둥글게 만드는 속성*/
    color: #8C8C8C;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), /*박스에 그림자를 만들 때 사용*/
        0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img{
    position: relative; /*태그의 위치를 살짝 변경하고 싶을 때 position: relative를 사용*/
    top: -50px;
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie .movie__title,
.movie .movie__year {
    margin: 0;
    font-weight: 300;
}

.movie .movie__title{
    margin-bottom: 5px;
    font-size: 24px;
    color: #232323;
    font-weight: bolder;
}

.movie .movie__year,
.movie .movie__genres {
    color: #8C8C8C;
}

.movie .movie__genres{
    list-style: none;
    display: flex;
    margin: 0px;
    padding: 0;
    margin: 5px 0px;
    font-weight: 400;
}

.movie__genres li {
    margin-right: 10px;
    font-size: 14px;
}